.slick-slider {
  opacity: 0;
  overflow: hidden;
}
.slick-slider.slick-initialized {
  opacity: 1;
}

.slick-slide {
  outline: none;
}

/* slick Dots */
.slick-dotted {
  padding-bottom: 30px;
}
@media screen and (min-width: 768px), print {
  .slick-dotted {
    padding-bottom: 50px;
  }
}

.slick-dots {
  position: absolute;
  bottom: 0;
  text-align: center;
  width: 100%;
}
.slick-dots li {
  cursor: pointer;
  display: inline-block;
  width: 12px;
  height: 12px;
  font-size: 10px;
  background-color: var(--gray);
  border-radius: 50%;
  margin: 0 15px;
}
.slick-dots li.slick-active button {
  background-color: var(--blue);
}
.slick-dots li button {
  border: 0;
  background: var(--gray);
  border-radius: 50%;
  display: block;
  height: 12px;
  width: 12px;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: transparent;
  padding: 5px;
  cursor: pointer;
}

/* slick Arrows */
.slick-arrow {
  position: absolute;
  display: block;
  z-index: 10;
  cursor: pointer;
  top: auto;
  bottom: calc(50% + 30px);
  transform: translateY(50%);
  width: 50px;
  height: 50px;
  background-color: var(--black);
}
.slick-arrow::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 12px;
  height: 12px;
  border-top: 2px solid var(--white);
  border-right: 2px solid var(--white);
  transform: translate(-50%, -50%) rotate(45deg);
  margin-top: -5px;
  transform-origin: left;
}

.slick-prev {
  transform: scale(-1, 1) translateY(50%);
  left: 0;
}

.slick-next {
  right: 0;
}

@media screen and (min-width: 768px), print {
  .js-slider-banner .slick-slide {
    margin: 0 20px;
  }
}
