.page-numbers {
  display: inline-block;
  padding: 14px 14px 16px 14px;
  margin: 0 1.5%;
  line-height: 1;
  text-decoration: none;
  font-size: 1.8rem;
  position: relative;
  color: #000;
}
.page-numbers.dots {
  margin: 0;
}
.page-numbers:not(.dots):not(.next):not(.prev)::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40px;
  height: 40px;
  border: 1px solid var(--gray-light);
  z-index: -1;
  border-radius: 2px;
  transition: 0.2s;
}
@media screen and (min-width: 768px), print {
  .page-numbers:not(.dots):not(.next):not(.prev):hover {
    color: #fff;
  }
  .page-numbers:not(.dots):not(.next):not(.prev):hover::before {
    border: 1px solid var(--gray-light);
    background-color: var(--gray-light);
  }
}
.page-numbers:not(.dots):not(.next):not(.prev).current {
  color: #fff;
}
.page-numbers:not(.dots):not(.next):not(.prev).current::before {
  border: 1px solid var(--gray-light);
  background-color: var(--gray-light);
}
.page-numbers.next, .page-numbers.prev {
  border: none;
  position: relative;
  text-decoration: underline;
}
.page-numbers.next::after, .page-numbers.prev::after {
  content: "";
  position: absolute;
  top: 50%;
  width: 6px;
  height: 6px;
  transform-origin: center;
  margin-top: -1px;
}
@media screen and (min-width: 768px), print {
  .page-numbers.next, .page-numbers.prev {
    transition: 0.1s;
  }
  .page-numbers.next:hover, .page-numbers.prev:hover {
    text-decoration: none;
  }
}
.page-numbers.next {
  padding-right: 20px;
}
.page-numbers.next::after {
  right: 5px;
  border-top: 1px solid #000;
  border-right: 1px solid #000;
  transform: rotate(45deg) translateY(-50%);
}
.page-numbers.prev {
  padding-left: 20px;
}
.page-numbers.prev::after {
  content: "";
  left: 5px;
  border-top: 1px solid #000;
  border-left: 1px solid #000;
  transform: rotate(-45deg) translateY(-50%);
}
@media screen and (max-width: 767px) {
  .page-numbers {
    margin: 0;
  }
  .page-numbers.next {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }
  .page-numbers.prev {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
  }
  .page-numbers:not(.next):not(.prev) {
    display: none;
  }
}

